<template>
  <div id="message" style="margin-top:60px;">
    <my-header :child="type"></my-header>
     <section class="main">
      <div class="des-top"></div>
       <div class="des-content">
        <div class="content" style="margin-top: -16px;">
          <div class="message-content">
            <div class="dis">
                <div class="liuyan">
                  <div v-for="(p,index) in 10" :key="index">
                    <ul style="float:left;">
                      <li class="name">刘晓冬</li>
                      <li>来啊一起玩啊</li>
                    </ul>
                    <ul style="float:right;text-align:right;">
                      <li class="name">我</li>
                      <li>滚，自己玩去</li>
                    </ul>
                  </div>
                </div>
            </div>
            <el-form ref="form" :model="form" label-width="80px" style="width:60%;">
                <el-form-item label="姓名" style="width:40%;">
                  <el-input v-model="form.name"></el-input>
                </el-form-item>

                <el-form-item label="留言内容" style="width:80%;">
                  <el-input :rows="5" type="textarea" v-model="form.desc"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary">留言</el-button>
                  <el-button>重置</el-button>
                </el-form-item>
              </el-form>



          </div>
        </div>
       </div>
      <div class="des-bottom"></div>
    </section>
  </div>
</template>

<script>
import Header from "../header";

export default {
  name: "app",
  components: {
    myHeader: Header
  },
  data() {
    return {
      msg: "我的博客",
      type: "message",
      form: {
        name: "",
        desc: ""
      }
    };
  }
};
</script>

<style scoped>
.message-content {
  width: 100%;
  height: 650px;
  border-radius: 10px;
  background-color: #184b67;
  padding: 10px;
}
.liuyan {
  width: 917px;
  margin: 0 auto;
  color: white;
  height: 350px;
  overflow: auto;
}
.liuyan > div {
  width: 96%;
  background-color: aliceblue;
  color: black;
  border-radius: 5px;
  margin: 10px auto;
  padding: 10px;
  box-sizing: border-box;
}
.liuyan > div::after {
  display: block;
  content: "";
  clear: both;
}
/* .liuyan>div:hover{
  transform: scale(1.2);
} */
.liuyan > div ul {
  width: 100%;
}
.dis {
  width: 900px;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 5px;
  background: #7e7e7ab0;
}
</style>






